import CalculatorService from "./CalculatorService";
import DiscountCalculatorService from "./DiscountCalculatorService";
import Book from "@/models/Book";
import BasketItem from "@/models/BasketItem";

const BasketService = {
  addBook(book: Book, basket: Book[]): Book[] {
    const newBasket = [...basket];
    newBasket.push(book);
    return newBasket;
  },

  removeBook(book: Book, basket: Book[]): Book[] {
    const newBasket = [...basket];
    const index = newBasket.findIndex(inBasketBook => inBasketBook.id === book.id);

    if (index === -1) {
      return newBasket;
    }

    newBasket.splice(index, 1);
    return newBasket;
  },

  getTotalBeforeDiscount(basket: Book[]): number {
    return CalculatorService.getTotal(basket);
  },

  getTotalDiscount(basket: Book[]): number {
    return DiscountCalculatorService.getTotalDiscount(basket);
  },

  getTotalAfterDiscount(basket: Book[]): number {
    const total = CalculatorService.getTotal(basket);
    const discount = DiscountCalculatorService.getTotalDiscount(basket);

    return total - discount;
  },

  getBasketItemSummary(basket: Book[]): Record<string, BasketItem> {
    return basket.reduce((accu: Record<string, BasketItem>, current) => {
      if (!accu[current.id]) {
        accu[current.id] = {
          id: current.id,
          title: current.title,
          quantity: 1,
          totalPrice: current.price
        } as BasketItem;
      } else {
        accu[current.id].quantity += 1;
        accu[current.id].totalPrice += current.price;
      }

      return accu;
    }, {});
  }
};

export default BasketService;
