


















import Vue from "vue";
import { mapActions } from "vuex";
import Book from "@/models/Book";

export default Vue.extend({
  name: "BookItem",
  props: {
    book: {
      type: Object,
      required: true
    }
  },
  methods: {
    ...mapActions({
      addBookToBasket: "addBookToBasket"
    }),
    addBook(item: Book) {
      this.addBookToBasket(item);
    }
  }
});
