














































import Vue from "vue";
import { mapState, mapActions } from "vuex";
import { RootState } from "../store";
import BasketService from "@/services/BasketService";
import Book from "@/models/Book";
import Checkout from "@/components/Checkout.vue";
import TotalItem from "@/components/TotalItem.vue";

export default Vue.extend({
  name: "Basket",
  components: {
    TotalItem
  },
  computed: {
    ...mapState<RootState>({
      processStarted: (state: RootState) => {
        return state.basket.length > 0;
      },
      basket: (state: RootState) => {
        return BasketService.getBasketItemSummary(state.basket);
      },
      totalPriceBeforeDiscount: (state: RootState) => {
        return BasketService.getTotalBeforeDiscount(state.basket);
      },
      totalDiscount: (state: RootState) => {
        return BasketService.getTotalDiscount(state.basket) * -1;
      },
      totalAfterDiscount: (state: RootState) => {
        return BasketService.getTotalAfterDiscount(state.basket);
      }
    })
  },
  methods: {
    ...mapActions({
      removeBookFromBasket: "removeBookFromBasket"
    }),
    removeBook(book: Book) {
      this.removeBookFromBasket(book);
    },
    checkout() {
      this.$modal.show(
        Checkout,
        {},
        {
          width: "100%",
          height: "70%",
          pivotY: 1.0,
          scrollable: true
        }
      );
    }
  }
});
