import Book from "@/models/Book";

const BookService = {
  async getBooks(): Promise<Book[]> {
    const fetchCatalogueErrorMsg =
      "It seems there is a problem getting all the books. Please contact admin.";

    const headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");

    try {
      const request = new Request(process.env.VUE_APP_API_URL, {
        method: "GET",
        ...headers
      });

      const response = await window.fetch(request);

      if (response.status < 200 || response.status > 300) {
        throw new Error(fetchCatalogueErrorMsg);
      }

      const jsonResult = await response.json();

      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      return (jsonResult.books as unknown[]).map((rawBook: any) => {
        return new Book(rawBook.id, rawBook.cover, rawBook.title, rawBook.price);
      });
    } catch (error) {
      throw new Error(fetchCatalogueErrorMsg);
    }
  }
};

export default BookService;
