import Vue from "vue";
import Vuex, { StoreOptions } from "vuex";
import Book from "@/models/Book";
import BookService from "@/services/BookService";
import BasketService from "@/services/BasketService";

Vue.use(Vuex);

export interface RootState {
  catalogue: Book[];
  catalogueError: string;
  basket: Book[];
}

const store: StoreOptions<RootState> = {
  state: {
    catalogue: [],
    catalogueError: "",
    basket: []
  },
  mutations: {
    updateCatalogue(state, data) {
      state.catalogue = data;
    },
    updateCatalogueError(state, error: string) {
      state.catalogueError = error;
    },
    addBookToBasket(state, item: Book) {
      state.basket = BasketService.addBook(item, state.basket);
    },
    removeBookFromBasket(state, item: Book) {
      state.basket = BasketService.removeBook(item, state.basket);
    },
    clearBasket(state) {
      state.basket = [];
    }
  },
  // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
  actions: {
    async getCatalogue({ commit }) {
      try {
        const books = await BookService.getBooks();
        commit("updateCatalogue", books);
      } catch (e) {
        commit("updateCatalogueError", e.message);
      }
    },
    addBookToBasket({ commit }, book: Book) {
      commit("addBookToBasket", book);
    },
    removeBookFromBasket({ commit }, book: Book) {
      commit("removeBookFromBasket", book);
    },
    clearBasket({ commit }) {
      commit("clearBasket");
    }
  }
};

export default new Vuex.Store<RootState>(store);
