





































































import Vue from "vue";
import Transaction from "@/models/Transaction";
import BasketService from "@/services/BasketService";
import { mapActions } from "vuex";

export default Vue.extend<Vue, any, any, any>({
  name: "Receipt",
  props: {
    transaction: {
      type: Object as () => Transaction,
      required: true
    }
  },
  computed: {
    transactionItems: function() {
      return BasketService.getBasketItemSummary(this.transaction.books);
    }
  },
  methods: {
    ...mapActions({
      clearBasket: "clearBasket"
    }),
    reset() {
      this.$emit("close");
      this.clearBasket();
    }
  }
});
