










import Vue from "vue";
import { mapState, mapActions } from "vuex";
import { RootState } from "@/store";
import BookItem from "./BookItem.vue";

export default Vue.extend({
  name: "Catalogue",
  components: {
    BookItem
  },
  computed: {
    ...mapState<RootState>({
      catalogue: (state: RootState) => state.catalogue,
      catalogueError: (state: RootState) => state.catalogueError
    })
  },
  methods: {
    ...mapActions({
      getCatalogue: "getCatalogue"
    })
  },
  created() {
    this.getCatalogue();
  }
});
