















































import Vue from "vue";
import { mapState } from "vuex";
import { RootState } from "../store";
import BasketService from "@/services/BasketService";
import Receipt from "@/components/Receipt.vue";
import Transaction from "@/models/Transaction";

export default Vue.extend({
  name: "Checkout",
  data: () => {
    return {
      amountReceived: 0
    };
  },
  computed: {
    ...mapState<RootState>({
      basket: (state: RootState) => {
        return state.basket;
      },
      totalPriceBeforeDiscount: (state: RootState) => {
        return BasketService.getTotalBeforeDiscount(state.basket);
      },
      totalDiscount: (state: RootState) => {
        return BasketService.getTotalDiscount(state.basket) * -1;
      },
      totalAfterDiscount: (state: RootState) => {
        return BasketService.getTotalAfterDiscount(state.basket);
      }
    }),
    changes: function() {
      return this.amountReceived - (this as any).totalAfterDiscount;
    }
  },
  methods: {
    close() {
      this.$emit("close");
    },
    pay() {
      this.close();

      const transaction = new Transaction(
        (this as any).basket,
        (this as any).totalPriceBeforeDiscount,
        (this as any).totalDiscount,
        (this as any).totalAfterDiscount,
        this.amountReceived,
        this.changes,
        true
      );

      this.$modal.show(
        Receipt,
        {
          transaction
        },
        {
          width: "80%",
          height: "auto",
          scrollable: true
        }
      );
    }
  }
});
