import Book from "@/models/Book";

class Transaction {
  id!: number;
  books!: Book[];
  amountPayableBeforeDiscount!: number;
  discount!: number;
  amountPayableAfterDiscount!: number;
  amountReceived!: number;
  changes!: number;
  success!: boolean;

  constructor(
    books: Book[],
    amountPayableBeforeDiscount: number,
    discount: number,
    amountPayableAfterDiscount: number,
    amountReceived: number,
    changes: number,
    success: boolean
  ) {
    this.id = Date.now();
    this.books = books;
    this.amountPayableBeforeDiscount = amountPayableBeforeDiscount;
    this.discount = discount;
    this.amountPayableAfterDiscount = amountPayableAfterDiscount;
    this.amountReceived = amountReceived;
    this.changes = changes;
    this.success = success;
  }
}

export default Transaction;
